header {
    display: flex;
    align-items: center;
    min-height: 200px;
    background-clip:content-box;
    background-repeat: no-repeat;
    width: 100%;
    /* background-size:cover; */
    overflow: hidden;
    background-attachment: fixed;
    background-position:right top;
  }
  
  header h1 {
    font-size: 40px;
    color: var(--menu-color)
  }

  header div {
    padding: 2em;
    color: var(--text-accent);
  }
  
  header div *{
    margin: 10px
  }