


:root {
  --menu-color  :  #DB2400;  /* Menu color */
  --menu-accent : #F5C2B8;    /* Menu selected/active */
  --menu-contrast : #424242;            /* Footer background */
  --background-color : white;           /* Background */
  --background-accent : #e2e2e2 ;       /* Side bars */
  --text-accent :  #666666;               /* Footer headers and meny text */
  --text-color  : #616161;              /* Normal text color */
  --text-faint : #e4e4e4;  /* Footer/Nav text */
  --text-constrast : #ff4081;           /* URLs */
  text-align: justify;
  background-color: var(--background-accent);
}

a {
  color : var(--text-constrast)
}
html,
body {
  margin: 0;
  font-family: 'Cabin', 'Roboto', 'Helvetica', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.body-block {
  background-color: var(--background-color);
  margin-left: 30px; 
  margin-right: 2em; 
  padding: 10px 20px;
  padding-bottom: 100px;
  flex: 1;
  background-clip: padding-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

h1 {
  font-weight:500
}
