
nav {
  box-sizing: border-box;
  overflow: hidden;
  width: 100%;
  background-color: var(--menu-color);
  margin: 0;
  padding: 0 0 0 30px;
  /* font-family: sans-serif; */
  font-size: 14px;
  line-height: 30px;
}

nav a {
  float: left;
  display: block ;
  text-decoration: none;
  text-align: center;
  padding: 1.3em 1em;
  font-weight: 500;
  text-transform: uppercase;
  color: var(--text-faint);
}

/* nav a.active 
nav a:hover {
  background-color: var(--menu-accent);
} */

nav a:hover,
nav a.active {
  background-color: var(--menu-accent);
  color: var(--text-color);
}

