ul.team {
    list-style-type: none;
    margin: 0;
    padding: 0 0 0 1em;
    display: flex;
    flex-wrap: wrap;
    gap: 50px 50px; /*row column*/

}

li.member {
box-sizing: border-box;
    text-align: center;
   width: 300px;
   height: 300px
}

li.member img.picture {
    object-fit: cover;
    width: 150px;
    height: 150px;
    clip-path: circle(50%);
}

li.member a {
    /* margin: 5px; */
    margin-left: 5px;
}

li.member a img {
    margin: 5px;
    margin: auto;
    height: 15px;
    width: 15px;
    object-fit: scale-down;
}
  

li.member h4 {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 15px;
}

li.member h5 {
    margin-top: 15px;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 600;
}

li.member h6 {
    margin-top: 0px;
    font-size: 14px;
    font-weight: 400;
}