footer {
    background-color: var(--menu-contrast);
    padding: 0px 0px 50px 0px;
    width: 100%;
    overflow: hidden;
    color:  var(--text-faint);
    margin: 0;
    overflow: hidden;
}

footer h5 {
    color: var(--text-accent);
}

footer div {
    float: left;
    display: block ;
    text-decoration: none;
    text-align: left;
    padding: 10px 30px;
    
}

footer div a {
    display: block;
    color:  var(--text-faint);
    text-decoration: none;
}

footer img {
    height: 15px;
    width: 15px;
    object-fit: scale-down;
}

footer p{
    margin: 0;
}
